<template>
    <div class="form-input">
        <label :for="inputName" class="material" :class="{ focused: focused }">{{ label }}</label>
        <template v-if="!multiline">
            <input
              type="text"
              :id="inputName"
              :name="inputName"
              :placeholder="placeholder"
              class="material"
              :class="{ focused: focused, error: hasError }"
              @click="hasFocus = true"
              @blur="hasFocus = false"
              @focus="hasFocus = true"
              v-model="value" />
        </template>
        <template v-if="multiline">
            <textarea
              :id="inputName"
              :name="inputName"
              :placeholder="placeholder"
              :rows="lines"
              class="material"
              :class="{ focused: focused, error: hasError }"
              @click="hasFocus = true"
              @blur="hasFocus = false"
              @focus="hasFocus = true"
              v-model="value"></textarea>
        </template>
        <label class="error" v-show="validationMessage && hasError">{{ validationMessage }}</label>
    </div>
</template>

<script>
export default {
  name: 'material-input',
  props: {
    inputName: {
      type: String
    },
    label: {
      type: String
    },
    multiline: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String
    },
    lines: {
      type: Number
    },
    valueFunction: {
      type: Function
    },
    commit: {
      type: String
    },
    validationFunction: {
      type: Function
    },
    validationMessage: {
      type: String
    }
  },
  data: function () {
    return {
      hasFocus: false,
      value: '',
      valid: true
    }
  },
  watch: {
    value: function (newValue, oldValue) {
      if (this.valueFunction) {
        this.valueFunction(this.commit, newValue)
      }

      if (this.validationFunction) {
        this.valid = this.validationFunction(newValue)
      }
    }
  },
  computed: {
    focused: function () {
      return this.hasFocus || this.value !== ''
    },
    hasError: function () {
      return this.focused && !this.valid
    }
  }
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';

    .form-input {
        display: flex;
        flex-direction: column;
        width: 75%;
        text-align: left;
        margin-left: auto;
        margin-right: auto;
        margin-top: 15px;

        input, textarea {
            &.material {
                border: 0;
                border-bottom: 1px solid lightgray;
                outline: none;
            }

            &.focused {
                border-bottom: 1px solid $secondaryColour;
            }

            &.error {
              border-bottom: 1px solid #ED6A5E;
              color: #ED6A5E;
            }
        }

        label {
            position: relative;
            cursor: text;
            transition: top 0.2s ease 0s;

            &.material {
                top: $lineHeight;
            }

            &.focused {
                top: 0;
            }

            &.error {
              color: #ED6A5E;
              font-style: italic;
            }
        }
    }
</style>



